<template>
    <div class="home" ref="area_scroll">
        <div class="header">
            <div class="address_map" @click="toAddress">
                <i class="glyphicon glyphicon-map-marker">
                    <span>{{address}}</span><i class="glyphicon glyphicon-chevron-down"></i>
                </i>
            </div>
            <div class="search_wrap" :class="{'fixedview':showFilter}" @click="$router.push('/search')">
              <div class="shop_search">
                <i class="glyphicon glyphicon-search"></i>
                 
              </div>
            </div>
        </div>
        <div id="container">
          <!--  -->
          <mt-swipe :auto="4000" class="swiper">
            <mt-swipe-item v-for="(img,index) in swipeImgs" :key=index>
              <img :src="img" alt="_blank">
            </mt-swipe-item>
          </mt-swipe>
          <!--  -->
          <mt-swipe :auto="0" class="entries">
            <mt-swipe-item v-for="(entry,i) in entries" :key=i>
              <div class="foodentry" v-for="(item,index) in entry" :key="index">
                <div class="img_wrap">
                  <img :src="item.image" alt="No">
                  <span>{{item.name}}</span>
                </div>
              </div>
            </mt-swipe-item>
          </mt-swipe>
        </div>
        <!--  -->
        <div class="shoplist-title"></div>
        <filterV :filterData="filterData" @searchFixed="showFilterView" @update="update"></filterV>
        <!--  -->
        <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"
          :auto-fill='false' :bottomPullText="bottomPullText ">
          <div class="shoplist">
          <shop v-for="(item,index) in restaurants" :key="index" :restaurant="item.restaurant"></shop>
        </div>
        </mt-loadmore>
        
    </div> 
</template>
<script>
import { Swipe, SwipeItem, Loadmore} from 'mint-ui';
import filterV from "../components/filterview"
import shop from "../components/shop"
import BScroll from 'better-scroll'
export default {
    name:'home',
    components:{
      filterV,
      shop
    },
    data(){
      return{
        swipeImgs:[],
        entries:[],
        filterData:null,
        showFilter: false,
        restaurants:[],
        allLoaded:false,
        bottomPullText:'',
        page:1,
        size:5,
        data:null,
        shopInput:''
      }
    },
    computed:{
        address(){
            return this.$store.getters.address
        },
        city(){
          return this.$store.getters.location.addressComponent.city
        }
    },
    methods:{
        initScroll(){
          this.scroll=new BScroll(this.$refs.area_scroll,)
        },
        toAddress(){
            this.$router.push({name:"address",params:{city:this.city}})
        },
        getData(){
          this.$axios.get('/api/profile/shopping').then(res=>{
             this.swipeImgs=res.data.swipeImgs;
             this.entries=res.data.entries;
          })
          this.$axios.get('/api/profile/filter').then(res=>{
            this.filterData=res.data
          })
          //
          this.$axios.post('/api/profile/restaurants/1/5').then(res=>{
            console.log(res.data)
            this.restaurants=res.data
          })
          this.loadData();
        },
        loadData() {
          this.page = 1;
          this.allLoaded = false;
          this.bottomPullText = "";
          // 
          this.$axios
            .post(`/api/profile/restaurants/${this.page}/${this.size}`, this.data)
            .then(res => {
              // console.log(res.data);
              this.$refs.loadmore.onTopLoaded();
              this.restaurants = res.data;
            });
        },
        showFilterView(isShow) {
          this.showFilter = isShow;
        },
        loadTop() {
            this.page = 1;
            this.allLoaded = false;
            this.bottomPullText = "";
            // 
            this.$axios
              .post(`/api/profile/restaurants/${this.page}/${this.size}`, this.data)
              .then(res => {
                // console.log(res.data);
                this.$refs.loadmore.onTopLoaded();
                this.restaurants = res.data;
        });
          
        },
        loadBottom() {
            if(this.allLoaded==false){
              this.page++;
              this.$axios.post(`/api/profile/restaurants/${this.page}/${this.size}`).then(res=>{
              this.$refs.loadmore.onBottomLoaded();
              if (res.data.length > 0) {
              res.data.forEach(item => {
                this.restaurants.push(item);
              });
              if (res.data < this.size) {
                this.allLoaded = true;
                this.bottomPullText = "";
              }
            } else {
              // 
              this.allLoaded = true;
              this.bottomPullText = "";
            }
          })
            }
  // 
          this.allLoaded = true;// 
        },
      update(condition) {
      // console.log(condition);
      this.data = condition;
      this.loadData();
    },
    searchShop(){
        var resList=[]
        resList=this.restaurants.filter(item=>{
                    return item.restaurant.name.includes(this.shopInput)
                })
        return resList;
    }
    },
    created(){
      this.getData();
    }
}
</script>
