<template>
  <div>
      <div class="head">
          <headbar :isLeft="true" title="" ></headbar>
          <div class="search_header">
              <form class="search_wrap">
                  <i class="fa fa-search"></i>
                  <input type="text" v-model="keyword" placeholder=" ">
                   <button></button>
              </form>
          </div>
      </div>
      <div class="shop" v-if="result">
          <div>
              <SI :data="result.restaurants"></SI>
              <SI :data="result.words"></SI>
          </div>
      </div>
  </div>
</template>

<script>
import headbar from '../components/HeadBar'
import SI from '../components/SearchIndex' 
export default {
    name:'search',
    components:{
        headbar,
        SI

    },
    data(){
        return {
            keyword:'',
            result:null
        }
    },
    watch:{
        keyword(){
            this.keywordChange()
        }
    },
    methods:{
        keywordChange(){
            this.$axios(`/api/profile/typeahead/${this.keyword}`)
            .then(res=>{
                console.log(res.data)
                this.result=res.data
            })
            .catch(err=>{
                this.result=null
            })
        }
    }
}
</script>

